.App {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto minmax(0, 1fr);
    justify-items: center;
    height: 100vh;
    padding: 0 1rem 1rem 1rem;
    box-sizing: border-box;
}


.App h1 {
    grid-column: 2;
    margin: 0;
    align-self: center;
}